import React from 'react';
import styles from './feedback.module.css'

class Feedback extends React.Component {
    // static defaultProps = {
    //     good: 0,
    //     neutral: 0,
    //     bad: 0
    // }
    state = {
        good: this.props.good,
        neutral: this.props.neutral,
        bad: this.props.bad
    }
    
    buttonClickGood = () => {
        this.setState(prevState => ({
            good: prevState.good + 1
        }))
    }
     buttonClickNeutral = () => {
        this.setState(prevState => ({
            neutral: prevState.neutral + 1
        }))
    }
     buttonClickBad = () => {
        this.setState(prevState => ({
            bad: prevState.bad + 1
        }))
    }
    
    countTotalFeedback() {
        let totalFeedBack = this.state.good + this.state.neutral + this.state.bad
        return totalFeedBack
    }
    
    countPositiveFeedbackPercentage() {
        if (!this.countTotalFeedback()) return
        let positivePercentage = Math.round((this.state.good / this.countTotalFeedback()) * 100)
        return positivePercentage
    }
    render() {
        return (
            <div className={styles.Fb_container}>
                <span>Please leave feedback</span>
                <div className={styles.button_container}>
                    <button type="button" onClick={this.buttonClickGood}>Good</button>
                    <button type="button" onClick={this.buttonClickNeutral}>Neutral</button>
                    <button type="button" onClick={this.buttonClickBad}>Bad</button>
                </div>
                
                <div className={styles.statistics_container}>
                    <span>Statistics</span>
                    <span>Good:{this.state.good}</span>
                    <span>Neutral:{this.state.neutral}</span>
                    <span>Bad:{this.state.bad}</span>
                </div>
                <div className={styles.results_container}>
                    <span>Total:{this.countTotalFeedback()}</span>
                    <span>Positive feedback:{ this.countPositiveFeedbackPercentage()}%</span>
                </div>
                
            </div>
        )
    }
}

export default Feedback